@import "../utils";

.page {
  color: $color-gray;

  &__body--with-menu {
    overflow: hidden;
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;

    transform: translateX(-100%);
    transition: transform 0.3s;

    &:target {
      transform: translateX(0);
    }
  }

  &__section {
    margin-bottom: 64px;

    @include onDesktop {
      margin-bottom: 104px;
    }
  
    &-title {
      margin: 0 0 32px;
      font-weight: bold;
      font-size: 36px;
      line-height: 36px;
      text-align: center;
      letter-spacing: -1.5px;
      color: #000;

      @include onDesktop {
        margin-bottom: 64px;
        font-size: 48px;
        line-height: 48px;
      }

      &::after {
        content: "";
        display: block;
        margin: 4px auto 0;
        width: 23px;
        height: 16px;
        background-image: url(../images/crown.svg);
        background-position: center;
        background-size: contain;
        background-repeat: no-repeat;
      }
    }
  }
}
